<template>
  <div class="np_wrap">
    <ul class="np_box">
      <li class="play_title">
        <strong>Playlist Title</strong>
        <div
          class="play_info"
          :style="`background-image:url(${musicData.songImg})`"
        ></div>
        <div class="playL">
          <span>{{ musicData.title }}</span>
          <span>{{ musicData.artist }}</span>
        </div>
        <div class="playR">
          <ul>
            <li
              class="liked_icon"
              @click="
                $store.dispatch('FAV_CLICK', this.musicData.playId),
                  npFAV_UPDATE()
              "
              ref="like"
            >
              <div class="liked_text">
                {{ this.$store.state.favorited[this.musicData.playId] }}
              </div>
              <img
                v-if="this.musicData.favorited == false"
                class="favOff"
                src="../img/favNone.svg"
                alt=""
              />
              <img
                v-if="this.musicData.favorited == true"
                class="favOn"
                src="../img/favIcon.svg"
                alt=""
              />
            </li>
            <li class="moreMenu" @click="moreOn">
              <img src="../img/more.svg" alt="" />
              <ul v-if="more == 'On'" class="more_wrap">
                <li class="more_list">좋아요</li>
                <li class="more_list">내 플레이리스트 추가</li>
                <li class="more_list">가사 보기</li>
                <li class="more_list">공유</li>
              </ul>
            </li>
          </ul>
        </div>
      </li>
      <li class="artist_title">
        <strong>아티스트 상세정보</strong>
        <div
          class="artist_img_wrap"
          :style="`background-image:url(${musicData.artistImg})`"
        >
          <div class="artist_img"></div>
        </div>
        <div class="artist_info">
          <p>{{ musicData.artist }}</p>
          <span>월별 리스너 8,381,619명</span>
          <span>
            아티스트 상세설명 Lorem ipsum dolor sit amet consectetur adipisicing
            elit. Doloremque obcaecati ducimus iste facilis possimus, sequi nisi
            asperiores beatae fugiat, laudantium eius ratione mollitia delectus!
            Voluptatum, vero nostrum ipsa a distinctio amet aliquid omnis
            sapiente ex nulla minus nobis veritatis eaque soluta fugiat
            delectus, illum, magni nam repellendus cumque? Nemo, eaque!
          </span>
          <button class="followBtn">팔로우하기</button>
        </div>
      </li>
      <li class="credit_title">
        <strong>크레딧</strong>
        <div class="credit_info">
          <ul>
            <li>
              <p>{{ musicData.artist }}</p>
              <span>메인 아티스트, 작사가</span>
            </li>
            <li>
              <p>작곡가</p>
              <span>작곡가</span>
            </li>
            <li>
              <p>프로듀서</p>
              <span>프로듀서</span>
            </li>
            <li>
              <p>출처</p>
            </li>
          </ul>
          <button class="followBtn">팔로우하기</button>
        </div>
      </li>
      <li class="nextPlay_none" v-if="this.listOn == false">
        <strong>재생목록이 비어 있습니다</strong>
        <button>새로운 곡을 검색해보세요</button>
      </li>
      <li class="nextPlay" v-else>
        <strong>다음 재생 항목</strong>
        <div class="np_list">
          <div class="np_play_hover">
            <img src="../img//playW.svg" alt="" />
          </div>
          <div
            class="np_img"
            :style="`background-image:url(${this.next.songImg})`"
          ></div>
          <span>{{ this.next.title }}</span>
          <span>{{ this.next.artist }}</span>
        </div>
      </li>
    </ul>
  </div>
</template>

<script>
export default {
  mounted() {
    this.musicData = this.$store.state.music[0];
    this.emitter.on("Title", (a) => {
      this.musicData = a;
      this.listOn = false;
    });
    this.emitter.on("nsTitle", (b) => {
      this.musicData = b;
    });
    this.emitter.on("fav", (b) => {
      this.npFaved = b;
    });

    this.emitter.on("plClick", (c) => {
      this.musicData = c;
      let pcHere = this.$store.state.playlist.findIndex(
        (obj) => obj.title == this.musicData.title
      );
      if (pcHere == this.pl.length - 1) {
        pcHere = -1;
      }
      this.next = this.pl[pcHere + 1];
    });

    this.emitter.on("lbList", (c) => {
      this.loadId = 0;
      this.pl = c;
      this.musicData = this.pl[0];
      this.next = c[1];
      if (this.pl.length > 1) {
        this.listOn = true;
      } else if (this.pl.length < 1) {
        this.listOn = false;
      }
    });

    this.emitter.on("plNext", (d) => {
      this.musicData = d;
      let npHere = this.$store.state.playlist.findIndex(
        (obj) => obj.title == this.musicData.title
      );

      this.loadId++;
      if (npHere == this.pl.length - 1) {
        npHere = -1;
        this.loadId = 0;
      }
      this.next = this.pl[npHere + 1];
      return;
    });
    this.emitter.on("shuffleOn", (e) => {
      this.musicData = e;
    });
  },
  name: "NowPlay",
  data() {
    return {
      loadId: 1,
      more: "Off",
      listOn: false,
      pl: [{}],
      next: "",
      musicData: {},
      npFaved: false,
    };
  },
  methods: {
    npFAV_UPDATE() {
      let favText = document.querySelector(".liked_text").innerText;
      let favInfo = this.$store.state.favorited[this.musicData.playId];
      const plHere = this.$store.state.playlist.findIndex(
        (obj) => obj.title == this.musicData.title
      );

      if (favInfo == false) {
        favText = false;
      } else if (favInfo == true) {
        favText = true;
      }

      if (favText == false) {
        this.musicData.favorited = false;
        this.$store.state.playlist.splice(plHere, 1);
      } else if (favText == true) {
        this.musicData.favorited = true;
        this.$store.state.playlist.push(
          this.$store.state.music[this.musicData.playId]
        );

        this.emitter.emit("plUpdate", this.$store.state.playlist);
      }
    },
  },
};
</script>

<style>
.np_wrap ::-webkit-scrollbar-thumb {
  background: #535353;
}
.np_wrap {
  width: 420px;
  height: calc(100% - 100px);
  background: #090909;
  overflow: hidden;
}
.np_box {
  width: 100%;
  height: 100%;
  border-radius: 8px;
  padding: 10px 20px 10px 20px;
  background: #111111;
  overflow-y: scroll;
}
.play_title {
  width: 100%;
  height: 460px;
}
.play_title strong {
  font-size: 16px;
  margin: 0;
}
.play_info {
  width: 100%;
  height: 366px;
  margin-top: 20px;
  border-radius: 10px;
  background: white;
  background-size: cover;
  background-repeat: no-repeat;
}
.playL {
  margin-top: 10px;
  float: left;
}
.playL span {
  font-size: 24px;
  color: #fff;
  display: block;
  text-align: left;
  font-family: "pretendard-bold";
}
.playL span:nth-of-type(2) {
  font-size: 18px;
  color: #535353;
  font-family: "pretendard-regular";
}

.playR {
  margin-top: 28px;
  float: right;
}
.playR li {
  float: left;
}
.playR .moreMenu {
  width: 24px;
  height: 24px;
}
.playR .moreMenu img {
  width: 24px;
  height: 24px;
}

.artist_title {
  background: #1f1f1f;
  border-radius: 10px;
  position: relative;
  margin-top: 40px;
}
.artist_title strong {
  position: absolute;
  top: 16px;
  left: 6px;
  color: #fff;
  font-size: 16px;
}
.artist_img_wrap {
  background: red;
  border-radius: 10px 10px 0px 0px;
  background-size: cover;
  background-repeat: no-repeat;
}
.artist_img {
  background: linear-gradient(#0000005e 0%, #ffee0000 100%);
  width: 100%;
  height: 260px;
  border-radius: 10px 10px 0px 0px;
}
.artist_info {
  position: relative;
  width: 100%;
  text-align: left;
  padding: 16px;
}
.artist_info p {
  color: #fff;
}
.artist_info span {
  color: #858585;
  font-size: 17px;
  display: block;
}
.artist_info span:nth-of-type(2) {
  font-size: 16px;
  margin-top: 20px;
  text-overflow: ellipsis;
  overflow: hidden;
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 3;
}
.followBtn {
  width: 110px;
  height: 36px;
  border-radius: 50px;
  background: none;
  border: 1px solid #7e7e7e;
  color: #fff;
  position: absolute;
  top: 28px;
  right: 16px;
}
.np_wrap button {
  transition-duration: 0.1s;
}
.np_wrap button:hover {
  scale: 1.02;
}
.credit_title {
  width: 100%;
  background: #1f1f1f;
  border-radius: 10px;
  margin-top: 16px;
  padding: 16px;
  position: relative;
}
.credit_title strong {
  margin: 0;
}
.credit_title button {
  top: 72px;
}
.credit_info {
  text-align: left;
  line-height: 24px;
  margin-top: 20px;
}
.credit_info li,
.credit_info ul {
  margin: 0;
  padding-left: 0;
  margin-top: 20px;
}
.credit_info li p {
  color: #fff;
  margin-bottom: 0;
}
.credit_info li span {
  color: #858585;
}

.nextPlay {
  position: relative;
  width: 100%;
  height: 140px;
  background: #1f1f1f;
  margin-top: 16px;
  border-radius: 10px;
  padding: 16px;
}

.nextPlay strong {
  margin: 0;
}
.nextPlay_none {
  position: relative;
  width: 100%;
  height: 110px;
  background: #1f1f1f;
  margin-top: 16px;
  border-radius: 10px;
  padding: 16px;
}
.nextPlay_none button {
  position: absolute;
  width: 200px;
  height: 36px;
  border-radius: 50px;
  top: 60px;
  left: 20px;
  background: none;
  border: 1px solid #7e7e7e;
  color: #fff;
}
.np_list {
  width: 100%;
  height: 72px;
  border-radius: 2px;
  padding: 10px 0px;
  margin-top: 10px;
  position: relative;
}
.np_list:hover {
  background: rgb(38, 38, 38);
}
.np_list span {
  margin-left: 82px;
  display: block;
  text-align: left;
  font-family: "pretendard-regular";
  color: #fff;
  margin-top: 2px;
}

.np_img {
  width: 52px;
  height: 52px;
  border-radius: 2px;
  background-size: cover;
  background-repeat: no-repeat;
  float: left;
  margin-left: 10px;
}
.np_play_hover {
  position: absolute;
  left: 10px;
  width: 52px;
  height: 52px;
  background: rgba(0, 0, 0, 0.6);
  opacity: 0;
}
.np_play_hover img {
  width: 16px;
  height: 16px;
  margin-left: 2px;
  margin-top: 18px;
}
.np_list:hover .np_play_hover {
  opacity: 1;
}
.np_play_hover img:hover {
  scale: 1.05;
}
@media (max-width: 768px) {
  .np_wrap {
    display: none;
  }
}
@media (min-width: 768px) and (max-width: 1000px) {
  .np_wrap {
    width: 320px;
  }
  .play_title {
    height: 380px;
  }
  .play_info {
    height: 266px;
  }

  .artist_img_wrap {
    height: 180px;
  }
  .artist_info {
    width: 100%;
    height: 220px;
  }
  .followBtn {
    top: 63px;
    right: 16px;
  }
  .artist_info span:nth-of-type(1) {
    width: 120px;
    margin-left: 0px;
    line-height: 24px;
  }

  .credit_info li {
    width: 120px;
  }
}
</style>