<template>
  <div class="mix_wrap">
    <h4>회원님을 위한 믹스 &amp; 추천</h4>
    <swiper
      :slidesPerView="3"
      :breakpoints="{
        '768': {
          slidesPerView: 3,
          spaceBetween: 20,
        },
        '1000': {
          slidesPerView: 4,
          spaceBetween: 20,
        },
        '1280': {
          slidesPerView: 4,
          spaceBetween: 20,
        },
        '1600': {
          slidesPerView: 5,
          spaceBetween: 20,
        },
      }"
      :spaceBetween="20"
      :grabCursor="true"
      :freeMode="true"
      :pagination="false"
      :modules="modules"
      class="mySwiper"
    >
      <swiper-slide v-for="i in $store.state.mix" :key="i">
        <div
          class="commend_box"
          :style="`background-image : url(${i.mixImage})`"
        >
          <div class="play_box_wrap">
            <div>
              <p>{{ i.comId }}</p>
              <div class="play_box">
                <div class="play_box_img">
                  <img
                    src="https://github.com/simjsik/Hackbo_portfolio/assets/39624384/4a784618-5801-43ac-8df8-46cb6370ffa9"
                    alt=""
                  />
                </div>
              </div>
              <div class="play_more"></div>
            </div>
          </div>
          <div
            class="mix_cover_wrap"
            :style="`background-image : url(${i.coverImg})`"
          ></div>
          <div class="content_wrap">
            <span class="under_on">{{ i.title }}</span>
            <span class="under_on">{{ i.artists }} 등</span>
          </div>
        </div>
      </swiper-slide>
    </swiper>
  </div>
</template>

<script>
import { Swiper, SwiperSlide } from "swiper/vue";

import "swiper/css";

import "swiper/css/free-mode";
import "swiper/css/pagination";

import { FreeMode, Pagination } from "swiper/modules";

export default {
  name: "MixCommend",
  data() {
    return {};
  },
  components: { Swiper, SwiperSlide },
  setup() {
    return {
      modules: [FreeMode, Pagination],
    };
  },
};
</script>

<style>
.mix_wrap {
  position: relative;
  text-align: left;
}
.mix_wrap h4 {
  font-size: 28px;
  font-family: "pretendard-bold";
  color: #fff;
}
.mix_wrap .commend_box {
  width: 100%;
  height: 100%;
  background-size: cover;
  background-repeat: no-repeat;
}
.commend_box:hover .play_box_wrap > div {
  opacity: 1;
}
.mix_wrap .play_box_wrap {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  z-index: 1;
}
.mix_wrap .mix_cover_wrap {
  width: 100%;
  height: 100%;
  position: absolute;
  background-size: cover;
  background-repeat: no-repeat;
}
.commend_box p {
  display: none;
}
.mix_wrap .content_wrap {
  position: absolute;
  bottom: -50px;
}
@media (max-width: 499px) {
  .mix_wrap .play_box_wrap > div {
    background: linear-gradient(#ffffff00 0%, rgba(0, 0, 0, 0.4) 100%);
    opacity: 1;
  }
}
@media (max-width: 600px) {
  .mix_wrap .play_box {
    width: 40px;
    height: 40px;
    bottom: 12px;
    left: 12px;
  }
  .mix_wrap .play_more {
    bottom: 15px;
    right: 12px;
  }
}
</style>