<template>
  <div class="ns_wrap">
    <h4>좋아하는 아티스트 최신 곡</h4>
    <swiper
      :slidesPerView="'2'"
      :spaceBetween="20"
      :grabCursor="true"
      :grid="{
        rows: 3,
      }"
      :pagination="false"
      :modules="modules"
      class="mySwiper"
    >
      <swiper-slide v-for="(ns, i) in $store.state.newmusic" :key="i">
        <div class="content_wrap">
          <div
            class="ns_list_wrap"
            @mouseover="nsLoadTitle"
            @click="nsLoadTitle_click"
          >
            <div class="ns_song">
              <p class="playId">{{ ns.playId }}</p>
              <div class="ns_play_hover" @click="nsSetAudio(), nsSendTitle()">
                <img src="../img//playW.svg" alt="" />
              </div>
              <div
                class="ns_img"
                :style="`background-image:url(${ns.songImg})`"
              ></div>
              <span class="under_on title">{{ ns.title }}</span>
              <span class="under_on artist">{{ ns.artist }}</span>
            </div>
          </div>
        </div>
      </swiper-slide>
    </swiper>
  </div>
</template>

<script>
import { Swiper, SwiperSlide } from "swiper/vue";

import "swiper/css";
import "swiper/css/grid";
import "swiper/css/free-mode";
import "swiper/css/pagination";

import { Grid, FreeMode, Pagination } from "swiper/modules";

export default {
  computed: {},
  name: "NewSong",
  components: { Swiper, SwiperSlide },
  setup() {
    return {
      modules: [Grid, FreeMode, Pagination],
    };
  },
  mounted() {},
  methods: {
    nsSendTitle() {
      if (window.innerWidth > 768) {
        this.emitter.emit("nsTitle", this.nsmusic);
      }
    },

    nsSetAudio() {
      this.nsmusic = this.nsSaveMusic;
    },

    nsLoadTitle(e) {
      if (window.innerWidth > 768) {
        let nsSet = e.currentTarget;

        const nsSetId = nsSet.querySelector(".playId").innerText;
        this.nsSaveMusic = this.$store.state.music[nsSetId];
      }
    },
    nsLoadTitle_click(e) {
      if (window.innerWidth < 768) {
        this.detail = document.querySelector(".detail_wrap");
        this.detail.style.right = "0";
        let nsSet = e.currentTarget;

        const nsSetId = nsSet.querySelector(".playId").innerText;
        this.nsSaveMusic = this.$store.state.music[nsSetId];
        console.log(this.nsSaveMusic);
        this.emitter.emit("detail", this.nsSaveMusic);
      }
    },
  },
  data() {
    return {
      nsSaveMusic: {},
      nsmusic: {},
    };
  },
};
</script>

<style>
.playId {
  display: none;
}
.ns_img {
  position: absolute;
  z-index: -1;
  left: 0px;
  width: 50px;
  height: 50px;
  background-size: cover;
  background-repeat: no-repeat;
}
.ns_wrap {
  position: relative;
  width: 100%;
  height: 100%;
  text-align: left;
}
.ns_wrap .swiper {
  width: 100%;
  height: 100%;
  margin-left: auto;
  margin-right: auto;
}
.ns_wrap .swiper-wrapper {
  position: absolute;
  height: 100%;
  transform: translate3d(0px, 0px, 0px);
}
.ns_wrap .swiper-slide {
  margin-right: 20px;
}
.ns_wrap .content_wrap {
  width: 100%;
  bottom: 0;
}
.ns_list_wrap {
  border-bottom: 1px solid #282828;
}
.ns_wrap h4 {
  font-size: 28px;
  font-family: "pretendard-bold";
  color: #fff;
}

.ns_song {
  padding: 10px 0px;
  width: 100%;
  height: 70px;
  margin-left: 0px;
}

.ns_song:hover .ns_play_hover {
  opacity: 1;
}
.ns_song span {
  display: block;
  width: 50%;
  margin-left: 72px;
  text-overflow: ellipsis;
  overflow: hidden;
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 1;
  line-height: 24px;
}
.ns_play_hover {
  position: absolute;
  width: 50px;
  height: 50px;
  background: rgba(0, 0, 0, 0.6);
  opacity: 0;
}
.ns_play_hover img {
  width: 16px;
  height: 16px;
  margin-left: 18px;
  margin-top: 16px;
}
.ns_play_hover:hover .ns_play_hover img {
  scale: 1.05;
}
@media (max-width: 500px) {
  .ns_song span {
    margin-left: 60px;
  }
}
</style>