<template>
  <div class="recent_wrap">
    <h4>최근 재생한 항목</h4>
    <swiper
      :slidesPerView="3"
      :breakpoints="{
        '768': {
          slidesPerView: 3,
          spaceBetween: 20,
        },
        '1000': {
          slidesPerView: 4,
          spaceBetween: 20,
        },
        '1280': {
          slidesPerView: 4,
          spaceBetween: 20,
        },
        '1600': {
          slidesPerView: 5,
          spaceBetween: 20,
        },
      }"
      :spaceBetween="20"
      :grabCursor="true"
      :freeMode="true"
      :pagination="false"
      :modules="modules"
      class="mySwiper"
    >
      <swiper-slide v-for="i in this.$store.state.recent" :key="i">
        <div
          class="recent_box"
          @mouseover="loadTitle_hover"
          @click="loadTitle_click"
        >
          <div class="play_box_wrap">
            <div>
              <p class="playId">{{ i.playId }}</p>
              <div class="play_box" @click="setAudio()">
                <div class="play_box_img">
                  <img
                    src="https://github.com/simjsik/Hackbo_portfolio/assets/39624384/4a784618-5801-43ac-8df8-46cb6370ffa9"
                    alt=""
                  />
                </div>
              </div>
              <div class="play_more"></div>
            </div>
          </div>
          <div
            class="rec_cover_wrap"
            :style="`background-image : url(${i.songImg})`"
          ></div>
        </div>
        <div class="content_wrap">
          <span class="under_on title">{{ i.title }}</span>
          <span class="under_on artist">{{ i.artist }}</span>
          <span>{{ i.day }}</span>
        </div>
      </swiper-slide>
    </swiper>
  </div>
</template>

<script>
import { Swiper, SwiperSlide } from "swiper/vue";

import "swiper/css";

import "swiper/css/free-mode";
import "swiper/css/pagination";

import { FreeMode, Pagination } from "swiper/modules";
export default {
  computed: {
    // mapState(['state변수이름']) : 헬퍼함수
    // 를 이용한 변수 가져오기
  },
  mounted() {},
  name: "RecentBox",
  data() {
    return {
      saveMusic: {},
      music: {},
      idx: "",
    };
  },
  components: { Swiper, SwiperSlide },
  setup() {
    return {
      modules: [FreeMode, Pagination],
    };
  },
  methods: {
    setAudio() {
      this.music = this.saveMusic;
      this.emitter.emit("Title", this.music);
    },
    loadTitle_hover(e) {
      let get = e.currentTarget;
      const plId = get.querySelector(".playId").innerText;
      this.saveMusic = this.$store.state.music[plId];
    },
    loadTitle_click(e) {
      if (window.innerWidth < 768) {
        this.detail = document.querySelector(".detail_wrap");
        this.detail.style.right = "0";
        let get = e.currentTarget;
        const plId = get.querySelector(".playId").innerText;
        this.saveMusic = this.$store.state.music[plId];
        this.emitter.emit("detail", this.saveMusic);
      }
    },
    // // mapMutations 는 methods에 정의
    // ...mapMutations(["SET_TITLE,SET_ARTIST,SET_AUDIO"]),
    // changeTitle(e) {
    //   // mutation 호출
    //   // this.$store.commit('함수 이름','변경값')
    //   let get = e.currentTarget;
    //   const getTitle = get.querySelector(".title").innerText;
    //   this.$store.commit('SET_TITLE', getTitle);
    //   console.log(this.$store.state.playTitle)
    // },
  },
};
</script>

<style>
.recent_wrap {
  position: relative;
  text-align: left;
}
.recent_wrap h4 {
  font-size: 28px;
  font-family: "pretendard-bold";
  color: #fff;
}
.under_on:hover {
  text-decoration: underline;
}
.swiper {
  /* width: 100%; */
  height: 100%;
  overflow: inherit;
  margin-top: 20px;
}
.recent_box {
  width: 100%;
  height: 100%;
}
.recent_box:hover .play_box_wrap > div {
  opacity: 1;
}
.recent_box p {
  display: none;
}
.swiper-wrapper {
}
.swiper-slide {
  aspect-ratio: 1;
}
.play_box_wrap {
  background-size: cover;
  background-repeat: no-repeat;
}
.recent_wrap .play_box_wrap {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
}
.recent_wrap .play_box_wrap {
  background-image: url(https://github.com/simjsik/Hackbo_portfolio/assets/39624384/19288005-423c-43c2-b7c0-6c218207b3d1);
}
.rec_cover_wrap {
  width: 100%;
  height: 100%;
  background-size: cover;
  background-repeat: no-repeat;
}
.rec_cover_wrap div {
  width: 100%;
  height: 288px;
  background-size: cover;
  background-repeat: no-repeat;
  background: linear-gradient(#0000003f 0%, #ffffff00 50%, #0000003f 100%);
}
.content_wrap {
  position: absolute;
  bottom: -80px;
}
.content_wrap span {
  color: #fff;
  display: block;
  text-overflow: ellipsis;
  overflow: hidden;
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 1;
}
.content_wrap span:nth-of-type(2) {
  color: #858585;
  margin-top: 4px;
}
.content_wrap span:nth-of-type(3) {
  color: #595959;
  margin-top: 10px;
}

@media (min-width: 500px) and (max-width: 600px) {
  .recent_wrap .play_box {
    width: 40px;
    height: 40px;
    bottom: 12px;
    left: 12px;
  }
  .recent_wrap .play_more {
    bottom: 15px;
    right: 12px;
  }
}
@media (max-width: 768px) {
  .recent_wrap .play_box_wrap > div {
    background: linear-gradient(#ffffff00 0%, rgba(0, 0, 0, 0.4) 100%);
    opacity: 1;
  }
  .recent_wrap .play_box {
    width: 100%;
    height: 100%;
    bottom: 0px;
    left: 0px;
  }
  .play_box {
    display: none;
  }
  .play_box_img {
    display: none;
  }
  .play_more {
    display: none;
  }
}
</style>