<template>
  <div class="mb_library_wrap" @click="getLbData">
    <div class="mb_lb_top">
      <img
        class="lb_logo"
        ref="lb_logo"
        src="./img/lb_img.svg"
        alt="라이브러리"
      />
      <p ref="lb_title">내 라이브러리</p>
    </div>
    <swiper
      :slidesPerView="'auto'"
      :spaceBetween="20"
      :grabCursor="true"
      :direction="'vertical'"
      :pagination="false"
      :modules="modules"
      class="mySwiper"
    >
      <swiper-slide>
        <div class="mb_library_box" @click="getLbData">
          <p class="lbId">0</p>
          <div class="lb_img">
            <div class="my_lb_img"></div>
          </div>
          <div class="my_lb_text_box">
            <p class="lb_title">좋아요 표시한 곡</p>
            <span class="lb_like">
              <img class="favOn" src="../img/favIcon.svg" alt="" />
            </span>
            <p class="lb_sub">
              <span>
                좋아요 표시한 곡 · {{ $store.state.playlist.length }}곡
              </span>
            </p>
          </div>
        </div>
      </swiper-slide>
      <swiper-slide
        class="mb_library_box"
        v-for="(lb, i) in this.$store.state.library"
        :key="i"
        @click="getLbData"
      >
        <p class="lbId">{{ $store.state.library[i].lbId }}</p>
        <div class="lb_list_img_wrap">
          <div
            class="lb_list_img"
            :style="{
              backgroundImage: `url(${$store.state.music[0].songImg})`,
            }"
          ></div>
          <div
            class="lb_list_img"
            :style="{
              backgroundImage: `url(${$store.state.music[2].songImg})`,
            }"
          ></div>
          <div
            class="lb_list_img"
            :style="{
              backgroundImage: `url(${$store.state.music[3].songImg})`,
            }"
          ></div>
          <div
            class="lb_list_img"
            :style="{
              backgroundImage: `url(${$store.state.music[6].songImg})`,
            }"
          ></div>
        </div>
        <div class="lb_text_box">
          <p class="lb_title">{{ $store.state.library[i].title }}</p>
          <p class="lb_sub" v-if="this.$store.state.library[i].liked == true">
            <span class="lb_like">
              <img class="favOn" src="../img/favIcon.svg" alt="" />
            </span>
            <span>
              {{ $store.state.library[i].subtitle }}
            </span>
          </p>
          <p class="lb_sub" v-if="this.$store.state.library[i].liked == false">
            <span>
              {{ $store.state.library[i].subtitle }} ·
              {{ $store.state.library[i].userId }}
            </span>
          </p>
        </div>
      </swiper-slide>
    </swiper>
  </div>
</template>

<script>
import { mapState } from "vuex";
import { Swiper, SwiperSlide } from "swiper/vue";

import "swiper/css";

import "swiper/css/pagination";
import { FreeMode } from "swiper/modules";
export default {
  name: "LibRary",
  components: {
    Swiper,
    SwiperSlide,
  },
  setup() {
    return {
      modules: [FreeMode],
    };
  },
  computed: {
    ...mapState(["playlist"]),
  },
  data() {
    return {
      lbPlayList: "",
    };
  },
  methods: {
    getLbData() {
      let list = this.playlist;
      let listCopy = [...list];
      this.lbPlaylist = listCopy;
      this.emitter.emit("lbList", this.lbPlaylist);
    },
  },
};
</script>

<style>
@media (max-width: 768px) {
  .mb_library_wrap .swiper {
    position: absolute;
    top: 60px;
    width: 100%;
    height: 100%;
    text-align: left;
  }
  .mb_library_wrap .swiper-slide {
    height: 60px;
  }
  .mb_library_wrap .swiper-wrapper {
    background: #191919;
  }
  .mb_library_wrap .my_library_box {
    display: block;
  }
  .mb_library_wrap {
    position: absolute;
    top: 0;
    width: 100%;
    height: 100%;
    overflow: hidden;
    background: #191919;
    padding-left: 10px;
  }
  .mb_lb_top {
    position: absolute;
    left: 0;
    width: 100vw;
    height: 60px;
    font-size: 20px;
    text-align: left;
    line-height: 60px;
    background: #191919;
    padding-left: 10px;
    box-shadow: 0px 3px 10px #090909;
  }
  .mb_lb_top > p {
    color: #fff;
  }
  .mb_lb_top .lb_logo {
    margin-left: 0px;
    margin-bottom: 0px;
    margin-top: 16px;
    width: 28px;
    height: 28px;
  }
  .mb_library_box {
    height: 100%;
  }
  .mb_library_box .lb_img, 
  .mb_library_box .lb_list_img_wrap {
    width: 60px;
    height: 60px;
  }
  .mb_library_wrap .lb_list_img {
    width: 50%;
    height: 50%;
    background: blue;
    float: left;
    margin-right: 0px;
    background-size: cover;
    border-radius: 0px;
  }
  .mb_library_wrap .my_lb_text_box,
  .mb_library_wrap .lb_text_box {
    height: 100%;
    padding: 0px;
  }
  .mb_library_wrap .my_lb_text_box > p,  
  .mb_library_wrap .lb_text_box > p {
    line-height: 10px;
    padding-top: 14px;
  }
  .mb_library_wrap .my_lb_text_box > p:nth-of-type(2),
  .mb_library_wrap .lb_text_box > p:nth-of-type(2) {
    font-size: 12px;
    line-height: 6px;
    padding-top: 0;
  }
}
</style>