<template>
  <div class="detail_wrap" :style="{ backgroundColor: musicData.bgColor }">
    <div class="dt_back_btn" @click="close_detail"></div>
    <div class="detail_main">
      <div
        class="detail_box"
        :style="{ backgroundImage: 'url(' + musicData.songImg + ')' }"
      ></div>
      <div class="detail_title">
        <span>{{ musicData.title }}</span>
        <span>{{ musicData.artist }}의 곡</span>
      </div>
      <div class="detail_menu">
        <ul class="left_icon_wrap">
          <li>
            <div
              class="liked_icon"
              ref="like"
              @click="
                $store.dispatch('FAV_CLICK', musicData.playId), FAV_UPDATE()
              "
            >
              <div class="liked_text">
                {{ this.$store.state.favorited[musicData.Id] }}
              </div>
              <img
                v-if="musicData.favorited == false"
                class="favOff"
                src="../img/favNone.svg"
                alt=""
              />
              <img
                v-if="musicData.favorited == true"
                class="favOn"
                src="../img/favIcon.svg"
                alt=""
              />
            </div>
          </li>
          <li class="moreMenu" @click="moreOn">
            <img src="../img/more.svg" alt="" />
            <ul v-if="more == 'On'" class="more_wrap">
              <li class="more_list">좋아요</li>
              <li class="more_list">내 플레이리스트 추가</li>
              <li class="more_list">가사 보기</li>
              <li class="more_list">공유</li>
            </ul>
          </li>
        </ul>
        <li class="play_center">
          <div @click="shuOn" class="shuffle">
            <img
              class="sOff"
              v-if="shuffle == 'Off'"
              src="../img/shuffle.svg"
              alt=""
            />
            <img
              class="sOn"
              v-if="shuffle == 'On'"
              src="../img/shuffleOn.svg"
              alt=""
            />
          </div>
          <div class="pausePlay" @click="setAudio()">
            <div class="pauseCir"></div>
            <img
              class="play"
              src="https://github.com/simjsik/Hackbo_portfolio/assets/39624384/4a784618-5801-43ac-8df8-46cb6370ffa9"
              alt=""
            />
          </div>
        </li>
      </div>

      <div class="detail_playlist">
        <p>다음 곡과 셔플됨</p>
        <div class="detail_playlist_wrap">
          <div class="detail_playlist_cont" v-for="(dp, i) in plList" :key="i">
            <span>{{ dp.title }}</span>
            <p>{{ dp.artist }} ·</p>
          </div>
        </div>
        <span>더보기</span>
      </div>

      <div class="detail_include">
        <div class="detail_include_wrap">
          <p>수록된 작품</p>
          <swiper
            :slidesPerView="3"
            :breakpoints="{
              '768': {
                slidesPerView: 3,
                spaceBetween: 20,
              },
              '1000': {
                slidesPerView: 4,
                spaceBetween: 20,
              },
              '1280': {
                slidesPerView: 4,
                spaceBetween: 20,
              },
              '1600': {
                slidesPerView: 5,
                spaceBetween: 20,
              },
            }"
            :spaceBetween="20"
            :grabCursor="true"
            :freeMode="true"
            :pagination="false"
            :modules="modules"
            class="mySwiper"
          >
            <swiper-slide>
              <div class="dtMix_box">
                <div
                  class="dtMix_cover_wrap"
                  :style="`background-image : url(${this.$store.state.mix[0].mixImage})`"
                ></div>
              </div>
              <div class="content_wrap">
                <span class="under_on title">{{
                  this.$store.state.mix[0].title
                }}</span>
              </div>
            </swiper-slide>
            <swiper-slide>
              <div class="dtMix_box">
                <div
                  class="dtMix_cover_wrap"
                  :style="`background-image : url(${this.$store.state.mix[3].mixImage})`"
                ></div>
              </div>
              <div class="content_wrap">
                <span class="under_on title">{{
                  this.$store.state.mix[3].title
                }}</span>
              </div>
            </swiper-slide>
            <swiper-slide>
              <div class="dtMix_box">
                <div
                  class="dtMix_cover_wrap"
                  :style="`background-image : url(${this.$store.state.mix2[0].mymixImage})`"
                ></div>
              </div>
              <div class="content_wrap">
                <span class="under_on title">{{
                  this.$store.state.mix2[0].title
                }}</span>
              </div>
            </swiper-slide>
            <swiper-slide>
              <div class="dtMix_box">
                <div
                  class="dtMix_cover_wrap"
                  :style="`background-image : url(${this.$store.state.mix2[2].mymixImage})`"
                ></div>
              </div>
              <div class="content_wrap">
                <span class="under_on title">{{
                  this.$store.state.mix2[2].title
                }}</span>
              </div>
            </swiper-slide>
            <swiper-slide>
              <div class="dtMix_box">
                <div
                  class="dtMix_cover_wrap"
                  :style="`background-image : url(${this.$store.state.mix2[3].mymixImage})`"
                ></div>
              </div>
              <div class="content_wrap">
                <span class="under_on title">{{
                  this.$store.state.mix2[3].title
                }}</span>
              </div>
            </swiper-slide>
            <swiper-slide>
              <div class="dtMix_box">
                <div
                  class="dtMix_cover_wrap"
                  :style="`background-image : url(${this.$store.state.mix[4].mixImage})`"
                ></div>
              </div>
              <div class="content_wrap">
                <span class="under_on title">{{
                  this.$store.state.mix[4].title
                }}</span>
              </div>
            </swiper-slide>
          </swiper>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { Swiper, SwiperSlide } from "swiper/vue";

import "swiper/css";

import "swiper/css/free-mode";
import "swiper/css/pagination";

import { FreeMode, Pagination } from "swiper/modules";
export default {
  name: "DetailView",
  mounted() {
    this.emitter.on("detail", (d) => {
      this.musicData = d;
    });
    this.emitter.on("mbClick", (d) => {
      this.musicData = d;
    });
    this.emitter.on("shuffle", (d) => {
      this.shuffle = d;
    });
  },
  data() {
    return {
      plList: [
        {
          title: "Love wins all",
          artist: "아이유(IU)",
        },
        {
          title: "밤양갱",
          artist: "비비(BIBI)",
        },
        {
          title: "Chocolate",
          artist: "츄(CHUU)",
        },
        {
          title: "날 그만 잊어요",
          artist: "마크툽(Maktub)",
        },
        {
          title: "비의 랩소디",
          artist: "임재현",
        },
        {
          title: "Perfect Night",
          artist: "LE SSERAFIM(르세라핌)",
        },
      ],
      musicData: {},
      shuffle: "Off",
      more: "Off",
    };
  },
  methods: {
    FAV_UPDATE() {
      let favText = document.querySelector(".liked_text").innerText;
      let favInfo = this.$store.state.favorited[this.musicData.playId];

      if (favInfo == false) {
        favText = false;
      } else if (favInfo == true) {
        favText = true;
      }

      const plHere = this.$store.state.playlist.findIndex(
        (obj) => obj.title == this.musicData.title
      );
      if (favText == false) {
        this.musicData.favorited = false;
        this.$store.state.playlist.splice(plHere, 1);
      } else if (favText == true) {
        this.musicData.favorited = true;
        this.$store.state.playlist.push(
          this.$store.state.music[this.musicData.playId]
        );

        this.emitter.emit("plUpdate", this.$store.state.playlist);
      }
    },
    setAudio() {
      this.emitter.emit("Title", this.musicData);
    },
    close_detail() {
      this.detail = document.querySelector(".detail_wrap");
      this.detail.style.right = "100%";
    },
    shuOn: function () {
      if (this.shuffle == "Off") {
        this.shuffle = "On";
      } else if (this.shuffle == "On") {
        this.shuffle = "Off";
      }
      this.emitter.emit("detail_shuffle", this.shuffle);
    },
  },
  components: { Swiper, SwiperSlide },
  setup() {
    return {
      modules: [FreeMode, Pagination],
    };
  },
};
</script>

<style>
.detail_wrap {
  position: absolute;
  right: 100%;
  top: 0;
  width: 100%;
  z-index: 9;
  overflow: scroll;
  overflow-x: hidden;
  transition-duration: 0.3s;
}
.dt_back_btn {
  width: 40px;
  height: 40px;
  background: url(https://github.com/simjsik/Hackbo_portfolio/assets/39624384/9daaefc6-5522-4998-a84d-48a062f45b22);
  transform: rotate(90deg);
  position: absolute;
  margin-left: 10px;
  margin-top: 20px;
}
.detail_main {
  width: 100%;
  background: linear-gradient(
    #00000000 0%,
    rgb(9, 9, 9) 40%,
    rgb(9, 9, 9) 100%
  );
  padding-top: 10px;
  padding-bottom: 200px;
}
.detail_box {
  width: 70%;
  aspect-ratio: 1.05;
  background: white;
  border-radius: 2px;
  box-shadow: 0px 0px 20px #000;
  background-size: cover;
  background-repeat: no-repeat;
}
.detail_title {
  text-align: left;
  width: 100%;
  padding-left: 10px;
  margin-top: 20px;
}
.detail_title span {
  display: block;
  font-size: 24px;
  color: #fff;
  font-family: "pretendard-bold";
}
.detail_title span:nth-of-type(2) {
  color: #b0b0b0;
  font-size: 16px;
  font-family: "pretendard-regular";
  margin-top: 6px;
}
.detail_menu {
  width: 100%;
  height: 60px;
  position: relative;
}
.detail_menu .left_icon_wrap {
  position: absolute;
  top: 50%;
  transform: translateY(-23%);
  height: 100%;
  display: block;
  margin-left: 10px;
  margin-top: 0px;
}
.detail_menu .liked_icon {
  width: 24px;
  height: 24px;
  margin-right: 12px;
  margin-top: 0px;
}
.detail_menu .liked_icon .favOn {
  width: 100%;
  height: 100%;
}
.detail_menu .left_icon_wrap > li:nth-of-type(1) {
  margin-top: 2px;
}
.detail_menu .play_center {
  width: 100px;
  height: 100%;
  right: 10px;
  top: 50%;
  transform: translateY(-32%);
}
.detail_menu .shuffle {
  display: block;
  float: left;
  margin-right: 20px;
  margin-top: 5px;
}
.detail_menu .pausePlay {
  float: left;
  width: 40px;
  height: 40px;
}
.detail_menu .pausePlay img {
  width: 40px;
  height: 40px;
  margin-left: 0px;
}
.detail_playlist {
  width: 100%;
  height: 150px;
  text-align: left;
  padding-left: 10px;
  margin-top: 20px;
}
.detail_playlist > p {
  font-size: 14px;
  color: #fff;
}
.detail_playlist_wrap {
  width: 95%;
  margin-top: 6px;
  margin-left: 0px;
}
.detail_playlist_cont {
  display: block;
  float: left;
  font-size: 18px;
  color: #fff;
  margin-right: 10px;
  font-family: "pretendard-bold";
  margin-top: 6px;
}
.detail_playlist_cont span {
  float: left;
  margin-right: 10px;
}
.detail_playlist_cont p {
  float: left;
  color: #b0b0b0;
}
.detail_playlist > span {
  color: #b0b0b0;
  font-size: 18px;
  float: left;
  margin-top: 6px;
  font-family: "pretendard-bold";
}

.detail_include {
  width: 100%;
  aspect-ratio: 16/7.5;
  text-align: left;
  margin-left: 10px;
  margin-top: 40px;
  overflow: hidden;
  padding-right: 20px;
}
.detail_include .swiper {
  margin-left: 0px;
}
.detail_include_wrap > p {
  font-size: 20px;
  color: #fff;
  font-family: "pretendard-bold";
}
.dtMix_box {
  width: 100%;
  height: 100%;
}
.dtMix_cover_wrap {
  width: 100%;
  height: 100%;
  background-size: cover;
  background-repeat: no-repeat;
}
.detail_include .content_wrap {
  bottom: -32px;
}
@media (min-width: 768px) {
  .detail_wrap {
    display: none;
  }
}
</style>