import { createWebHistory, createRouter } from "vue-router";
import Library from './components/LibRary.vue';

const routes = [
  {
    path: "/library",
    component: Library,
  },
];

const router = createRouter({
  history: createWebHistory(),
  routes,
});

// App이 마운트되기 전에 라우터를 초기화하고 '/' 경로로 이동합니다.
router.isReady().then(() => {
  router.push('/');
});


export default router;
