<template>
  <div class="mymix_wrap">
    <h4>나만의 음악</h4>
    <swiper
      :slidesPerView="3"
      :breakpoints="{
        '768': {
          slidesPerView: 3,
          spaceBetween: 20,
        },
        '1000': {
          slidesPerView: 4,
          spaceBetween: 20,
        },
        '1280': {
          slidesPerView: 4,
          spaceBetween: 20,
        },
        '1600': {
          slidesPerView: 5,
          spaceBetween: 20,
        },
      }"
      :spaceBetween="20"
      :grabCursor="true"
      :freeMode="true"
      :pagination="false"
      :modules="modules"
      class="mySwiper"
    >
      <swiper-slide v-for="i in $store.state.mix2" :key="i">
        <div
          class="mymix_box"
          :style="`background-image : url(${i.mymixImage})`"
        >
          <div class="black_bg">
            <div class="play_box_wrap">
              <div>
                <p>{{ i.mixId }}</p>
                <div class="play_box">
                  <div class="play_box_img">
                    <img
                      src="https://github.com/simjsik/Hackbo_portfolio/assets/39624384/4a784618-5801-43ac-8df8-46cb6370ffa9"
                      alt=""
                    />
                  </div>
                </div>
                <div class="play_more"></div>
              </div>
            </div>
            <div
              class="mix_cover_wrap"
              :style="`background-image : url(${i.coverImg})`"
            >
              <span>{{ i.title }}</span>
            </div>
            <div class="content_wrap">
              <span class="under_on">{{ i.title }}</span>
              <span class="under_on">{{ i.artists }} 등</span>
            </div>
          </div>
        </div>
      </swiper-slide>
    </swiper>
  </div>
</template>

<script>
import { Swiper, SwiperSlide } from "swiper/vue";

import "swiper/css";

import "swiper/css/free-mode";
import "swiper/css/pagination";

import { FreeMode, Pagination } from "swiper/modules";

export default {
  name: "MyMix",
  data() {
    return {};
  },
  components: { Swiper, SwiperSlide },
  setup() {
    return {
      modules: [FreeMode, Pagination],
    };
  },
};
</script>

<style>
.mymix_wrap {
  position: relative;
  text-align: left;
}
.mymix_wrap h4 {
  font-size: 28px;
  font-family: "pretendard-bold";
  color: #fff;
}
.mymix_box {
  width: 100%;
  height: 100%;
  background-size: cover;
  background-repeat: no-repeat;
}
.mymix_box:hover .play_box_wrap > div {
  opacity: 1;
}
.mymix_wrap .play_box_wrap {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  z-index: 1;
}
.mymix_box p {
  display: none;
}
.black_bg {
  background: linear-gradient(#ffffff00 0%, rgba(0, 0, 0, 0.3) 100%);
  height: 100%;
}

.mymix_wrap .mix_cover_wrap span {
  font-size: 26px;
  font-family: "pretendard-bold";
  color: #fff;
  position: absolute;
  bottom: 14%;
  left: 24px;
  text-overflow: ellipsis;
  overflow: hidden;
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 1;
}
.mymix_wrap .mix_cover_wrap {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  background-size: cover;
  background-repeat: no-repeat;
  /* background: linear-gradient(#0000003f 0%, #ffffff00 50%, #0000003f 100%); */
}
.mymix_wrap .content_wrap {
  position: absolute;
  bottom: -50px;
}
@media (max-width: 499px) {
  .mymix_wrap .play_box_wrap > div {
    background: linear-gradient(#ffffff00 0%, rgba(0, 0, 0, 0.4) 100%);
    opacity: 1;
  }
}
@media (max-width: 600px) {
  .mymix_wrap .play_box {
    width: 40px;
    height: 40px;
    bottom: 12px;
    left: 12px;
  }
  .mymix_wrap .play_more {
    bottom: 15px;
    right: 12px;
  }
}
@media (max-width: 768px) {
  .mymix_wrap .mix_cover_wrap span {
    font-size: 1.4em;
  }
}
@media (min-width: 768px) and (max-width: 1000px) {
  .mymix_wrap .mix_cover_wrap span {
    font-size: 1.6em;
  }
}
@media (min-width: 1280px) and (max-width: 1600px) {
  .mymix_wrap .mix_cover_wrap span {
    font-size: 1.8em;
    bottom: 12%;
  }
}
</style>