<template>
  <div class="pl_wrap">
    <ul class="pl_box">
      <li class="nw_cont">
        <strong>지금 재생 중</strong>
        <div class="nw_list">
          <div class="nw_play_hover">
            <img src="../img//playW.svg" alt="" />
          </div>
          <div
            class="nw_img"
            :style="`background-image:url(${nowPlay.songImg})`"
          ></div>
          <span>{{ nowPlay.title }}</span>
          <span>{{ nowPlay.artist }}</span>
        </div>
      </li>
      <li class="pl_cont" v-if="plOn == true">
        <strong>다음 재생 목록</strong>
        <div class="pl_cont_wrap">
          <div class="pl_list" v-for="(ps, i) in this.plList" :key="i">
            <div class="pl_play_hover">
              <img class="pl_playImg" src="../img//playW.svg" alt="" />
              <p class="pl_id">{{ ps.playId }}</p>
            </div>
            <div
              class="pl_img"
              :style="`background-image:url(${ps.songImg})`"
            ></div>
            <p class="pl_id">{{ ps.playId }}</p>
            <span>{{ ps.title }}</span>
            <span>{{ ps.artist }}</span>
          </div>
        </div>
      </li>
    </ul>
  </div>
</template>

<script>
export default {
  mounted() {
    let plId = 1;
    this.nowPlay = this.$store.state.music[0];
    this.emitter.on("Title", (a) => {
      this.nowPlay = a;
      plId = 1;
      this.plOn = true;
      this.plList = [a];
    });

    this.emitter.on("nsTitle", (b) => {
      this.nowPlay = b;
      plId = 1;
      this.plOn = true;
      this.plList = [b];
    });

    this.emitter.on("lbList", (b) => {
      this.plOn = true;
      this.plList = b;
      this.nowPlay = b[0];
      plId = 1;
      let plCont = document.querySelectorAll(".pl_cont_wrap>div");
      for (let i = 0; i < plCont.length; i++) {
        plCont[i].style.marginTop = 0 + "px";
      }
    });

    this.emitter.on("plNext", (d) => {
      this.nowPlay = d;
      // 플레이리스트 번호 찾기
      let plHere = this.$store.state.playlist.findIndex(
        (obj) => obj.title == this.nowPlay.title
      );

      plId = plHere;
      let stpl = this.$store.state.playlist.length;
      let plCont = document.querySelectorAll(".pl_cont_wrap>div");
      for (let i = 0; i < plHere; i++) {
        plCont[i].style.marginTop = "-74px";
      }
      plCont[plHere].style.marginTop = "-74px";
      plCont[0].style.marginTop = "-74px";

      if (plId == stpl - 1) {
        for (let i = 0; i < plCont.length; i++) {
          plCont[i].style.marginTop = 0 + "px";
        }
      }
    });

    this.emitter.on("plPrev", (d) => {
      this.nowPlay = d;
      let plCont = document.querySelectorAll(".pl_cont_wrap>div");
      const plHere = this.$store.state.playlist.findIndex(
        (obj) => obj.title == this.nowPlay.title
      );
      plId = plHere;
      for (let i = 0; i < plCont.length; i++) {
        plCont[i].style.marginTop = 0 + "px";
      }
    });

    this.emitter.on("shuffleOn", (e) => {
      plId = 1;

      this.nowPlay = e;
      let plCont = document.querySelectorAll(".pl_cont_wrap>div");
      for (let i = 0; i < plCont.length; i++) {
        plCont[i].style.marginTop = 0 + "px";
      }
    });
  },
  updated() {
    let plHover = document.querySelectorAll(".pl_cont_wrap .pl_play_hover");
    let plCont = document.querySelectorAll(".pl_cont_wrap>div");

    plHover.forEach((el, index) => {
      el.onclick = () => {
        let plList = this.plList;
        this.nowPlay = plList[index];
        for (let i = 0; i <= index; i++) {
          plCont[i].style.marginTop = -74 + "px";
        }
        if (index == plCont.length - 1) {
          for (let i = 0; i < index; i++) {
            plCont[i].style.marginTop = 0 + "px";
            plCont[index].style.marginTop = 0 + "px";
          }
        }
        this.emitter.emit("plClick", this.nowPlay);
      };
    });
  },
  methods: {},
  name: "PlayList",
  data() {
    return {
      plOn: true,
      plList: [
        {
          playId: 0,
          title: "DNA",
          artist: "YENA(최예나)",
          artistImg:
            "https://github.com/simjsik/Hackbo_portfolio/assets/39624384/d2b9fa19-ca71-484a-8c46-5270e4a52349",
          artId: 6,
          songImg:
            "https://github.com/simjsik/Hackbo_portfolio/assets/39624384/40665ec7-5abe-4959-acab-614ad9ecb6ed",
          favorited: false,
          audio: "../music/sero-Cute.mp3",
          dur: "1:37",
        },
      ],
      nowPlay: {
        title: "DNA",
        artist: "YENA(최예나)",
        songImg:
          "https://github.com/simjsik/Hackbo_portfolio/assets/39624384/40665ec7-5abe-4959-acab-614ad9ecb6ed",
        audio: "../music/sero-Cute.mp3",
        dur: "1:37",
        Fav: false,
        Id: "0",
        idx: "1",
      },
    };
  },
};
</script>

<style>
.pl_id {
  display: none;
}
.pl_wrap {
  width: 420px;
  height: calc(100% - 100px);
  background: #090909;
}
.pl_box {
  width: 100%;
  height: 100%;
  border-radius: 8px;
  padding: 10px 20px 10px 20px;
  background: #111111;
}
.pl_box li:nth-of-type(2) {
  margin-top: 30px;
}
.pl_cont_wrap {
  width: 100%;
  height: 660px;
  overflow: hidden;
  overflow-y: scroll;
  position: relative;
}
.pl_list,
.nw_list {
  width: 100%;
  height: 72px;
  border-radius: 2px;
  margin-top: 4px;
  position: relative;
  transition-duration: 0.2s;
}
.pl_list {
  float: left;
}
.pl_list:nth-of-type(1) {
  margin-top: -74px;
}
.pl_list:hover,
.nw_list:hover {
  background: rgb(24, 24, 24);
  cursor: pointer;
}
.pl_list span,
.nw_list span {
  margin-left: 82px;
  display: block;
  text-align: left;
  font-family: "pretendard-regular";
  color: #fff;
  line-height: 26px;
  padding-top: 10px;
}
.nw_list span {
  color: #1cd75f;
}
.pl_list span:hover,
.nw_list span:hover {
  text-decoration: underline;
}
.pl_list span:nth-of-type(2),
.nw_list span:nth-of-type(2) {
  color: #535353;
  padding-top: 0px;
}
.pl_img,
.nw_img {
  width: 52px;
  height: 52px;
  border-radius: 2px;
  background-size: cover;
  background-repeat: no-repeat;
  float: left;
  margin-left: 10px;
  margin-top: 10px;
}
strong {
  display: block;
  margin-left: 10px;
  text-align: left;
  font-size: 18px;
  color: #fff;
  margin-bottom: 10px;
}

.nw_play_hover,
.pl_play_hover {
  position: absolute;
  left: 10px;
  width: 52px;
  height: 52px;
  background: rgba(0, 0, 0, 0.6);
  opacity: 0;
  margin-top: 10px;
}
.nw_play_hover img,
.pl_play_hover img {
  width: 16px;
  height: 16px;
  margin-left: 2px;
  margin-top: 18px;
}
.nw_play_hover img:hover,
.pl_play_hover img:hover {
  scale: 1.05;
}
.nw_list:hover .nw_play_hover,
.pl_list:hover .pl_play_hover {
  opacity: 1;
}
.fadeIn {
  opacity: 0;
}
@media (max-width: 768px) {
  .pl_wrap{
    display: none;
  }
}
@media (min-width: 769px) and (max-width: 1000px) {
  .pl_wrap {
    width: 320px;
    max-width: 509px;
  }
}
</style>