import { createStore } from 'vuex'
import library from './library.js'
import music from './music.js'
import recent from './recent.js'
import news from './news.js'
import mix from './mix.js'
import mix2 from './mix2.js'
import playlist from './playlist.js'
import artistinfo from './artistinfo.js'
import newmusic from './newmusic.js'
const store = createStore({
  state() {
    return {
      library: library,
      music: music,
      newmusic: newmusic,
      recent: recent,
      news: news,
      mix: mix,
      mix2: mix2,
      playlist: playlist,
      artistinfo: artistinfo,

      favorited: music.map(music => music.favorited),
    }
  },
  mutations: {
    FAV_ON(state, idx) {

      if (state.favorited[idx] == false) {
        state.favorited[idx] = true;
      } else if (state.favorited[idx] == true) {
        state.favorited[idx] = false;
      }
      // console.log(idx)
      // console.log(state.favorited)
    }
  },
  actions: {
    FAV_CLICK(context, idx){
      context.commit('FAV_ON', idx)
    }
  },
  getter: {
    favorited(state, idx){
      return state.favorited[idx]
    }
  }
})

export default store