<template>
  <div class="playBar_wrap">
    <div class="playing_wrap">
      <div class="playing">
        <div class="play_bar"></div>
        <div class="play_back"></div>
      </div>
    </div>
    <ul class="nowSong_wrap">
      <li>
        <div class="nowState" @click="mb_off()">
          <ul class="song_wrap">
            <li
              class="songImg"
              :style="`background-image:url(${musicData.songImg})`"
            ></li>
            <li class="songName">
              <p class="playSong">{{ musicData.title }}</p>
              <p class="playArtist">{{ musicData.artist }}</p>
            </li>
          </ul>

          <ul class="left_icon_wrap">
            <li>
              <div
                class="liked_icon"
                ref="like"
                @click="
                  $store.dispatch('FAV_CLICK', musicData.playId), FAV_UPDATE()
                "
              >
                <div class="liked_text">
                  {{ this.$store.state.favorited[musicData.Id] }}
                </div>
                <img
                  v-if="musicData.favorited == false"
                  class="favOff"
                  src="../img/favNone.svg"
                  alt=""
                />
                <img
                  v-if="musicData.favorited == true"
                  class="favOn"
                  src="../img/favIcon.svg"
                  alt=""
                />
              </div>
            </li>

            <li class="moreMenu" @click="moreOn">
              <img src="../img/more.svg" alt="" />
              <ul v-if="more == 'On'" class="more_wrap">
                <li class="more_list">좋아요</li>
                <li class="more_list">내 플레이리스트 추가</li>
                <li class="more_list">가사 보기</li>
                <li class="more_list">공유</li>
              </ul>
            </li>
          </ul>
        </div>
      </li>
      <li class="play_right">
        <p class="playTime">
          <span class="current">0:00</span>
          <span class="duration">&nbsp;/ {{ musicData.dur }}</span>
        </p>
        <div class="nowlist" @click="nl_On(), nlData()">
          <img
            class="Offimg"
            v-if="nlList == 'Off'"
            src="../img/thisSong.svg"
            alt=""
          />
          <img
            class="Onimg"
            v-if="nlList == 'On'"
            src="../img/thisSongOn.svg"
            alt=""
          />
          <p>지금 재생 중 보기</p>
        </div>
        <div class="playlist" @click="pl_On(), plData()">
          <img
            class="Offimg"
            v-if="plList == 'Off'"
            src="../img/nowList.svg"
            alt=""
          />
          <img
            class="Onimg"
            v-if="plList == 'On'"
            src="../img/nowListOn.svg"
            alt=""
          />
          <p>재생목록</p>
        </div>
        <div class="bluetooth" @click="btOn">
          <img
            class="Offimg"
            v-if="blue == 'Off'"
            src="../img/bluetooth.svg"
            alt=""
          />
          <img
            class="Onimg"
            v-if="blue == 'On'"
            src="../img//bluetoothOn.svg"
            alt=""
          />
          <p>기기에 연결하기</p>
        </div>
        <div class="volume_ico">
          <img class="vlOn Onimg" src="../img/volume.svg" alt="" />
          <img class="vlOff Offimg" src="../img/volumeOff.svg" alt="" />
          <p>음소거하기</p>
        </div>
        <div class="volume_bar_wrap">
          <div class="volume_bar">
            <input
              type="range"
              min="0"
              max="100"
              value="50"
              step="1"
              class="volumerange"
            />
          </div>
        </div>
      </li>
      <li class="play_center">
        <div @click="shuOn" class="shuffle">
          <img
            class="sOff"
            v-if="shuffle == 'Off'"
            src="../img/shuffle.svg"
            alt=""
          />
          <img
            class="sOn"
            v-if="shuffle == 'On'"
            src="../img/shuffleOn.svg"
            alt=""
          />
        </div>
        <div class="prevSong">
          <img src="../img/prev.svg" alt="" />
        </div>
        <div class="pausePlay" @click="playOn(), play()">
          <div class="pauseCir"></div>
          <img
            class="play"
            v-if="playing == false"
            src="../img/play.svg"
            alt=""
          />
          <img
            class="stop"
            v-if="playing == true"
            src="../img/stop.svg"
            alt=""
          />
          <audio
            id="main_audio"
            ref="main_audio"
            :src="`${musicData.audio}`"
          ></audio>
        </div>
        <div class="nextSong">
          <img src="../img/next.svg" alt="" />
        </div>
        <div @click="cycleOn" class="recycle">
          <img
            class="cOff"
            v-if="cycle == 'Off'"
            src="../img/recycle.svg"
            alt=""
          />
          <img
            class="cOn"
            v-if="cycle == 'On'"
            src="../img/recycleOn.svg"
            alt=""
          />
        </div>
      </li>
    </ul>
  </div>
  <div class="mb_pb_on" :style="{ backgroundColor: musicData.bgColor }">
    <div class="mb_pb_top">
      <div class="closeBtn" @click="mb_off"></div>
      <span>PlayList</span>
      <div class="mb_on_moreMenu" @click="mbmoreOn">
        <img src="../img/more.svg" alt="" />
      </div>
      <div class="mb_more_bg" @click="bgOff"></div>
      <div class="mb_more">
        <ul class="more_wrap">
          <li class="more_list">좋아요</li>
          <li class="more_list">내 플레이리스트 추가</li>
          <li class="more_list">가사 보기</li>
          <li class="more_list">공유</li>
        </ul>
      </div>
    </div>
    <!-- mbTop -->

    <div class="mb_main_wrap">
      <div
        class="mb_main"
        :style="{ backgroundImage: 'url(' + musicData.songImg + ')' }"
      ></div>
      <div class="mb_on_title">
        <p class="mb_on_song">{{ musicData.title }}</p>
        <p class="mb_on_artist">{{ musicData.artist }}</p>
        <div
          class="mb_on_fav"
          @click="$store.dispatch('FAV_CLICK', musicData.playId), FAV_UPDATE()"
        >
          <img
            v-if="musicData.favorited == false"
            class="favOff"
            src="../img/favNone.svg"
            alt=""
          />
          <img
            v-if="musicData.favorited == true"
            class="favOn"
            src="../img/favIcon.svg"
            alt=""
          />
        </div>
      </div>
      <div class="mb_playing_wrap">
        <div class="playing">
          <div class="mb_play_bar"></div>
          <div class="play_back"></div>
        </div>
        <p class="playTime">
          <span class="current">0:00</span>
          <span class="duration">{{ musicData.dur }}</span>
        </p>
      </div>
      <div class="mb_play_center">
        <div class="prevSong">
          <img src="../img/prev.svg" alt="" />
        </div>
        <div class="pausePlay" @click="playOn(), play()">
          <div class="pauseCir"></div>
          <img
            class="play"
            v-if="playing == false"
            src="../img/play.svg"
            alt=""
          />
          <img
            class="stop"
            v-if="playing == true"
            src="../img/stop.svg"
            alt=""
          />
        </div>
        <div class="nextSong">
          <img src="../img/next.svg" alt="" />
        </div>
      </div>
    </div>
    <div class="mb_bottom">
      <div class="bluetooth" @click="btOn">
        <img
          class="Offimg"
          v-if="blue == 'Off'"
          src="../img/bluetooth.svg"
          alt=""
        />
        <img
          class="Onimg"
          v-if="blue == 'On'"
          src="../img//bluetoothOn.svg"
          alt=""
        />
        <p>기기에 연결하기</p>
      </div>
    </div>
    <div class="mb_sub_wrap">
      <div class="mb_sub">
        <div
          class="mb_sub_img"
          :style="{ backgroundImage: 'url(' + musicData.artistImg + ')' }"
        ></div>
      </div>
      <div class="mb_sub_text">
        <p>{{ musicData.artist }}</p>
        <span>월별 리스너 8,381,619명</span>
        <span>
          아티스트 상세설명 Lorem ipsum dolor sit amet consectetur adipisicing
          elit. Doloremque obcaecati ducimus iste facilis possimus, sequi nisi
          asperiores beatae fugiat, laudantium eius ratione mollitia delectus!
          Voluptatum, vero nostrum ipsa a distinctio amet aliquid omnis sapiente
          ex nulla minus nobis veritatis eaque soluta fugiat delectus, illum,
          magni nam repellendus cumque? Nemo, eaque!
        </span>
      </div>
    </div>
  </div>
</template>

<script>
// import { mapMutations } from 'vuex';
export default {
  mounted() {
    const audio = document.querySelector("#main_audio");
    const prev = document.querySelector(".play_center .prevSong");
    const next = document.querySelector(".play_center .nextSong");
    const mbprev = document.querySelector(".mb_play_center .prevSong");
    const mbnext = document.querySelector(".mb_play_center .nextSong");
    const play = document.querySelector(".playing_wrap");
    const mbplay = document.querySelector(".mb_playing_wrap");
    const playbar = document.querySelector(".play_bar");
    const mbplaybar = document.querySelector(".mb_play_bar");
    this.nowstate = document.querySelector(".nowState");
    this.playWrap = document.querySelector(".playBar_wrap");
    this.mbMore = document.querySelector(".mb_more .more_wrap");
    this.mbbg = document.querySelector(".mb_more_bg");
    this.mbpb = document.querySelector(".mb_pb_on");

    this.musicData = this.$store.state.music[0];
    let trackId = 0;

    this.emitter.on("detail_shuffle", (d) => {
      this.shuffle = d;
    });
    this.emitter.on("resize", () => {
     this.playWrap.style.backgroundColor = this.musicData.bgColor
    });

    const loadMusic = (num) => {
      this.musicData = this.playlist[num];
      // 오디오 로드
      audio.src = this.musicData.audio;
    };

    const playAudio = () => {
      audio.addEventListener("canplaythrough", () => {
        audio.play();
        this.playing = true;
      });
    };
    const pauseAudio = () => {
      audio.addEventListener("canplaythrough", () => {
        audio.pause();
        this.playing = false;
        playbar.style.width = "0%";
        mbplaybar.style.width = "0%";
      });
    };

    const handlePlayChange = (data) => {
      pauseAudio();
      this.musicData = data;
      this.playlist = "";
      if (window.innerWidth < 768) {
        this.playWrap.style.backgroundColor = this.musicData.bgColor;
      }
    };

    const handlePlaylistChange = (data) => {
      pauseAudio();
      this.musicData = data[0];
      this.playlist = data;
      trackId = 0;
      loadMusic(trackId);
      if (window.innerWidth < 768) {
        this.playWrap.style.backgroundColor = this.musicData.bgColor;
      }
      this.emitter.on("plClick", (c) => {
        this.musicData = c;
        const plnum = this.$store.state.playlist.findIndex(
          (obj) => obj.title == c.title
        );
        trackId = plnum;
      });
    };

    const handlePrevButtonClick = () => {
      let plL = this.playlist.length;
      if (this.shuffle == "On" && plL > 0) {
        let preId = this.musicData.playId - 1;
        if (preId == -1) {
          preId = plL - 1;
        }
        loadMusic(preId);
        this.emitter.emit("shuffleOn", this.musicData);
        return;
      }
      if (plL > 0 && plL > trackId) {
        if (trackId == 0) {
          pauseAudio();
          return;
        }
        trackId--;
        loadMusic(trackId);
        if (window.innerWidth < 768) {
          this.playWrap.style.backgroundColor = this.musicData.bgColor;
        }
      }
      playbar.style.width = "0%";
      mbplaybar.style.width = "0%";
      if (plL < 1) {
        pauseAudio();
        return;
      }
      this.emitter.emit("plPrev", this.musicData);
    };

    const handleNextButtonClick = () => {
      let plL = this.playlist.length;
      if (this.shuffle == "On" && plL > 0) {
        let rdId = Math.floor(Math.random() * plL);
        loadMusic(rdId);
        playAudio();
        this.emitter.emit("shuffleOn", this.musicData);
        return;
      }
      if (plL > 0 && plL > trackId) {
        if (trackId == plL - 1) {
          trackId = 0;
          loadMusic(trackId);
        } else {
          trackId++;
          loadMusic(trackId);
          if (window.innerWidth < 768) {
            this.playWrap.style.backgroundColor = this.musicData.bgColor;
          }
        }
        playbar.style.width = "0%";
        mbplaybar.style.width = "0%";
        this.emitter.emit("plNext", this.musicData);
        playAudio();
      }
    };

    const handlePlayClick = (e) => {
      let mWid = play.clientWidth;
      let clX = e.offsetX;
      let clDur = audio.duration;
      audio.currentTime = (clX / mWid) * clDur;
      playAudio();
    };

    const mbhandlePlayClick = (e) => {
      let mbWid = mbplay.clientWidth;
      let clX = e.offsetX;
      let clDur = audio.duration;
      audio.currentTime = (clX / mbWid) * clDur;
      playAudio();
    };

    const handleAudioEnded = () => {
      audio.load();
      playbar.style.width = "0%";
      mbplaybar.style.width = "0%";

      let plL = this.playlist.length;
      if (this.shuffle == "On" && plL > 1) {
        trackId = 0;
        let rdId = Math.floor(Math.random() * plL);
        loadMusic(rdId);
        playAudio();
        this.emitter.emit("shuffleOn", this.musicData);
        return;
      }
      if (this.cycle == "On") {
        this.playing = true;
      } else if (this.cycle == "Off") {
        pauseAudio();
      }
      if (plL > 0 && plL >= trackId) {
        let plHere = this.$store.state.playlist.findIndex(
          (obj) => obj.title == this.musicData.title
        );
        trackId = plHere + 1;
        if (window.innerWidth < 768) {
          this.playWrap.style.backgroundColor =
            this.$store.state.playlist[trackId].bgColor;
        }
        loadMusic(trackId);
        playAudio();
        if (plL == trackId) {
          trackId = 0;
          this.playing = false;
          if (plL > 0) {
            pauseAudio();
          }
          this.musicData = this.playlist[trackId];
        }
        this.emitter.emit("plNext", this.musicData);
      }
    };

    // Event Listeners
    this.emitter.on("Title", handlePlayChange.bind(this));
    this.emitter.on("nsTitle", handlePlayChange.bind(this));
    this.emitter.on("npfav", (c) => {
      this.faved = c;
    });
    this.emitter.on("lbList", handlePlaylistChange.bind(this));
    play.addEventListener("click", handlePlayClick);
    mbplay.addEventListener("click", mbhandlePlayClick);
    prev.addEventListener("click", handlePrevButtonClick);
    next.addEventListener("click", handleNextButtonClick);
    mbprev.addEventListener("click", handlePrevButtonClick);
    mbnext.addEventListener("click", handleNextButtonClick);
    audio.addEventListener("ended", handleAudioEnded);
  },

  updated() {},
  name: "PlayBox",
  data() {
    return {
      playing: false,
      shuffle: "Off",
      cycle: "Off",
      more: "Off",
      mbmore: "Off",
      nlList: "Off",
      plList: "Off",
      nlOn: "",
      plOn: "",
      blue: "Off",
      mb: "Off",
      vol: "On",
      // on Off
      playlist: "",
      musicData: "",
    };
  },
  methods: {
    FAV_UPDATE() {
      let favText = document.querySelector(".liked_text").innerText;
      let favInfo = this.$store.state.favorited[this.musicData.playId];

      if (favInfo == false) {
        favText = false;
      } else if (favInfo == true) {
        favText = true;
      }

      const plHere = this.$store.state.playlist.findIndex(
        (obj) => obj.title == this.musicData.title
      );
      if (favText == false) {
        this.musicData.favorited = false;
        this.$store.state.playlist.splice(plHere, 1);
      } else if (favText == true) {
        this.musicData.favorited = true;
        this.$store.state.playlist.push(
          this.$store.state.music[this.musicData.playId]
        );

        this.emitter.emit("plUpdate", this.$store.state.playlist);
      }
    },
    plData() {
      this.emitter.emit("playList", this.plOn);
    },
    nlData() {
      this.emitter.emit("nowplay", this.nlOn);
    },
    play: function () {
      if (this.$refs.main_audio.paused == true) {
        this.$refs.main_audio.play();
        this.playing = true;
      } else if (this.$refs.main_audio.paused == false) {
        this.$refs.main_audio.pause();
        this.playing = false;
      }
    },
    moreOn: function () {
      if (this.more == "Off") {
        this.more = "On";
      } else if (this.more == "On") {
        this.more = "Off";
      }
    },
    mb_off() {
      if (this.mb == "Off") {
        this.mb = "On";
        this.mbpb.style.bottom = "0";
        this.mbpb.style.zIndex = "51";
      } else if (this.mb == "On") {
        this.mb = "Off";
        this.mbpb.style.bottom = "-100%";
      }
    },
    mbmoreOn: function () {
      this.mbmore = "On";
      this.mbMore.style.height = "40%";
      this.mbMore.style.bottom = "0";

      this.mbbg.style.opacity = "1";
      this.mbbg.style.zIndex = "2";
    },
    bgOff: function () {
      this.mbmore = "Off";
      this.mbMore.style.height = "0%";
      this.mbMore.style.bottom = "-100%";
      this.mbbg.style.opacity = "0";
      this.mbbg.style.zIndex = "-10";
    },
    nl_On: function () {
      if (this.nlList == "Off") {
        this.nlList = "On";
        this.nlOn = true;
        this.plOn = false;
        this.plList = "Off";
      } else if (this.nlList == "On") {
        this.nlList = "Off";
        this.nlOn = false;
      }
    },
    pl_On: function () {
      if (this.plList == "Off") {
        this.plList = "On";
        this.plOn = true;
        this.nlList = "Off";
        this.nlOn = false;
      } else if (this.plList == "On") {
        this.plList = "Off";
        this.plOn = false;
      }
      this.emitter.emit("nlClick", this.plOn);
    },
    btOn: function () {
      if (this.blue == "Off") {
        this.blue = "On";
      } else if (this.blue == "On") {
        this.blue = "Off";
      }
    },
    playOn: function () {
      if (this.$refs.main_audio.paused == false) {
        this.playing = false;
      } else if (this.$refs.main_audio.paused == true) {
        this.playing = true;
      }
    },
    shuOn: function () {
      if (this.shuffle == "Off") {
        this.shuffle = "On";
      } else if (this.shuffle == "On") {
        this.shuffle = "Off";
      }
      this.emitter.emit("shuffle", this.shuffle);
    },
    cycleOn: function () {
      if (this.cycle == "Off") {
        this.cycle = "On";
        this.$refs.main_audio.loop = true;
      } else if (this.cycle == "On") {
        this.cycle = "Off";
        this.$refs.main_audio.loop = false;
      }
    },
  },
};
</script>

<style>
.playBar_wrap {
  position: fixed;
  width: 100%;
  height: 80px;
  background: #090909;
  bottom: 0;
  font-family: "Pretendard-Regular";
  z-index: 50;
  transition-duration: 0.3s;
}

.playing_wrap {
  position: absolute;
  bottom: 80px;
  z-index: 10;
  width: 100%;
  height: 18px;
  overflow: hidden;
  transition-duration: 0.5s;
  transition-delay: 0.5s;
}
.playing {
  position: absolute;
  bottom: 0;
  z-index: 1;
  width: 100%;
  height: 3px;
  overflow: hidden;
  transition-duration: 0.5s;
}
.play_bar {
  position: absolute;
  z-index: 1;
  width: 0%;
  height: 18px;
  background: #1cd75f;
  margin: 0;
  transition-duration: 0.2s;
}
.play_back {
  position: absolute;
  width: 100%;
  height: 18px;
  background: #535353;
}

.playing:hover {
  cursor: pointer;
}
.playing_wrap:hover .playing {
  height: 18px;
}
.playing_wrap:hover {
  opacity: 1;
  height: 18px;
}

.song_wrap {
  margin-left: 0px;
  padding-left: 0px;
}
.songImg {
  width: 45px;
  height: 45px;
  float: left;
  background-size: cover;
  background-repeat: no-repeat;
}
.songName {
  width: 200px;
  float: left;
  margin-left: 12px;
  color: #b0b0b0;
  font-family: "Pretendard-Medium";
  text-align: left;
  line-height: 24px;
}
.songName > p:nth-of-type(1) {
  width: 100%;
  margin-left: 0px;
  color: #fff;
  text-overflow: ellipsis;
  overflow: hidden;
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 1;
}
.songName > p {
  margin-bottom: 0;
}
.left_icon_wrap {
  height: 28px;
  float: left;
  margin-top: 10px;
  margin-left: 40px;
  padding-left: 0px;
}
.left_icon_wrap > li {
  float: left;
}
.liked_icon {
  width: 16px;
  height: 16px;
  margin-right: 12px;
  margin-top: 6px;
}
.liked_text {
  display: none;
}
.liked_icon .favOff {
  opacity: 0.6;
}

.liked_icon .favOn {
  width: 16px;
  transition-duration: 0.1s;
}
.liked_icon:hover img {
  cursor: pointer;
  opacity: 1;
}
.liked_icon:hover .favOn {
  scale: 1.05;
}
.moreMenu {
  width: 28px;
  height: 28px;
}
.moreMenu:hover {
  cursor: pointer;
}
.moreMenu:active,
.liked_icon:active,
.play_right > div:active {
  opacity: 0.6;
}
.more_wrap {
  position: absolute;
  left: 280px;
  bottom: 50px;
  width: 200px;
  background: #2b2b2b;
  border-radius: 4px;
  padding: 10px 0px;
}
.more_list {
  line-height: 36px;
  margin-top: 8px;
  color: #b0b0b0;
}
.more_list:hover {
  background: rgb(36, 36, 36);
}
.moreMenu img {
  opacity: 0.6;
}
.moreMenu:hover img {
  opacity: 1;
}
.play_right {
  width: 418px;
  height: 24px;
  position: absolute;
  right: 20px;
  bottom: 30px;
}
.play_right > div {
  position: absolute;
}
.play_right > div:hover {
  cursor: pointer;
}
.play_right .Offimg {
  opacity: 60%;
}
.play_right > div:hover img {
  opacity: 1;
}
.play_right > div:hover .Onimg {
