<script setup>
import { ref } from "vue";
import axios from "axios";

const mb_result = ref([]);
const mb_result_copy = ref([]);

axios.get("/music.json").then((a) => {
  mb_result_copy.value = [...a.data];
});

function mb_search_this(searchtext) {
  const searchTextLow = searchtext.toLowerCase();
  if (!searchtext) {
    mb_result.value = [];
    return; // 검색 텍스트가 없으면 여기서 함수 종료
  }
  let searchResult = mb_result_copy.value.filter((data) => {
    return (
      data.title.toLowerCase().indexOf(searchTextLow) != -1 ||
      data.artist.toLowerCase().indexOf(searchTextLow) != -1
    );
  });
  mb_result.value = [...searchResult];
}
</script>
