<script>
import PlayBox from "./components/PlayBox.vue";
import MainDig from "./components/MainDig.vue";
import RecentBox from "./components/RecentBox.vue";
import MixCommend from "./components/MixCommend.vue";
import MyMix from "./components/MyMix.vue";
import MainAd from "./components/MainAd.vue";
import NewSong from "./components/NewSong.vue";
import PlayList from "./components/PlayList.vue";
import NowPlay from "./components/NowPlay.vue";
import DetailView from "./components/DetailView.vue";

import { mapState } from "vuex";

export default {
  computed: {
    ...mapState(["playlistCopy", "playlist"]),
    store() {
      return this.$store;
    },
  },
  mounted() {
    const body = document.querySelector("body");
    const noWrap = document.querySelector("#nowplay");
    const plWrap = document.querySelector("#playlist");
    const vr = document.querySelector(".volumerange");
    const vc = document.querySelector(".volume_ico");
    const audio = document.querySelector("#main_audio");
    const proBar = document.querySelector(".play_bar");
    const mbproBar = document.querySelector(".mb_play_bar");
    const playTime = document.querySelector(".current");
    const mbplayTime = document.querySelector(".mb_playing_wrap .current");
    this.recent = document.getElementById("recent");
    this.mix = document.getElementById("mix");
    this.mymix = document.getElementById("mymix");
    this.newSong = document.getElementById("newsong");
    this.mainDg = document.getElementById("mainDg");
    this.mainAd = document.getElementById("main_ad");
    this.footer = document.getElementById("footer");
    this.playWrap = document.querySelector(".playBar_wrap");
    this.detail = document.querySelector(".detail_wrap");
    this.mbsearch = document.querySelector(".mb_search_on");
    this.search = document.querySelector(".search_wrap");
    this.mb_search_click = document.querySelector(".mb_search_click");
    this.mb_search_click_box = document.querySelectorAll(
      ".mb_search_click_box"
    );

    this.mb_search_click_box.forEach((el, index) => {
      el.onclick = () => {
        let mbClick = this.$store.state.music;
        this.musicData = mbClick[index + 1];
        this.detail = document.querySelector(".detail_wrap");
        this.detail.style.right = "0";
        this.emitter.emit("mbClick", this.musicData);
      };
    });

    if (window.innerWidth < 769) {
      this.mbOn = true;
    } else if (window.innerWidth > 769) {
      this.mbOn = false;
    }

    this.emitter.on("playList", (c) => {
