    audio.addEventListener("timeupdate", () => {
      let curTime = audio.currentTime;
      let duration = audio.duration;
      let audioRatio = (curTime / duration) * 100;
      proBar.style.width = `${audioRatio}%`;
      mbproBar.style.width = `${audioRatio}%`;

      let curMin = Math.floor(curTime / 60);
      let curSec = Math.floor(curTime % 60);

      if (curSec < 10) curSec = `0${curSec}`;
      playTime.innerHTML = `${curMin}:${curSec}`;
      mbplayTime.innerHTML = `${curMin}:${curSec}`;
    });
