  updated() {
    this.mb_search_result_box = document.querySelectorAll(
      ".mb_search_result_box"
    );

    this.mb_search_result_box.forEach((el) => {
      el.onclick = () => {
        let reClick = this.$store.state.music;

        // 클릭 시 해당 요소의 span 찾기
        const resultTitleSpan = el.querySelector(".mb_search_result_title");

        // span이 존재한다면.
        if (resultTitleSpan) {
          // 해당 요소의 텍스트 값 가져오기. **trim으로 공백제거
          const resultTitle = resultTitleSpan.textContent.trim();

          // 해당 값이 존재하는 vuex데이터 찾기
          const resultData = this.$store.state.music.findIndex(
            (obj) => obj.title == resultTitle
          );

          // 데이터를 찾았다면
          if (resultData !== -1) {
            //데이터 할당
            this.musicData = reClick[resultData];
          }
        }

        this.detail = document.querySelector(".detail_wrap");
        this.detail.style.right = "0";
        this.emitter.emit("mbClick", this.musicData);
      };
    });
  },
