    handleVolumeChange() {
      const bar = document.querySelector(".volume_bar");
      const vol = document.querySelector(".volumerange");
      const audio = document.querySelector("#main_audio");

      bar.style.width = vol.value + "%";
      audio.volume = vol.value / 100;
      const vlOn = document.querySelector(".vlOn");
      const vlOff = document.querySelector(".vlOff");
      vlOn.style.display = vol.value == 0 ? "none" : "block";
      vlOff.style.display = vol.value == 0 ? "block" : "none";
    },
    handleMuteClick() {
      const vol = document.querySelector(".volumerange");
      const audio = document.querySelector("#main_audio");

      vol.value = vol.value > 0 ? 0 : 10;
      audio.volume = vol.value / 100;
      this.handleVolumeChange();
