    handleResize() {
      const winX = window.innerWidth;
      if (winX < 768) {
        this.mbOn = true;
        if (this.$route.path !== "/library") {
          this.resize();
          this.emitter.emit("resize");
        }
        this.recent.style.aspectRatio = "16 / 9";
        this.mix.style.aspectRatio = "16 / 8.5";
        this.mymix.style.aspectRatio = "16 / 8.5";
      } else if (winX >= 768 && winX < 1000) {
        this.$router.push("/");
        this.resize();
        this.playWrap.style.backgroundColor = "#090909";
        this.mbOn = false;
        if (this.plOn || this.nlOn) {
